@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville:wght@700&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;1,300;1,400&family=Roboto:wght@700&display=swap');


//this * style means we apply styles for all:
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
  color:rgb(25, 18, 68);
}

body {
  margin: 0;
  //you can delete first two (apple and etc), dont forget to add your imported typeface to a font-family
  font-family: 'Open Sans','Roboto', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 {
  font-family: 'Libre Baskerville';
}

h2 {
  font-family:'Libre Baskerville' ;
}

.container {
  max-width: 1240px;
  margin: auto;
}

.btn {
  color:beige;
  padding: 12px 24px;
  background-image: linear-gradient(45deg, #5651e5 0%, #709dff 100%);
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  transition: .4s;
  cursor: pointer;
}

.btn:active {
  transform: translate(4px);
  box-shadow: 0px 0px 1px rgba(0,0,0,.2);
  border-bottom:none;
}
